.game {
  height: 100%;
  display: flex;
  flex-direction: column;

  /*&__games-count {
    margin-top: 0;
  }*/

  &__screenshot {
    max-width: 100%;
    max-height: 50vh;

    &--hidden {
      display: none;
    }
  }

  &__answers {
    margin-bottom: 10px;
  }

  &__loader-wrap {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}